import './App.css';
import Navbar from './components/NavbarMenu/Navbar';
import { HashRouter, Routes, Route } from "react-router-dom";
import ProfileContainer, { withRouter } from './components/Profile/ProfileContainer';
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Headers/HeaderContainer';
import LoginPage from './components/Login/Login';
import React, { Suspense } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { initializeApp } from './redux/appReducer';
import Preloader from './components/common/Preloader/Preloader';

// import DialogsContainer from './components/Dialogs/DialogsContainer';
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
// const ProfileContainer , { withRouter } = React.lazy(() => import('./components/Profile/ProfileContainer'));


class App extends React.Component {

  componentDidMount() {
    this.props.initializeApp();
  }

  render() {

    // if  (!this.props.initialized) {
    //     return <Preloader /> 
    // } 

    return (
      <HashRouter>
        <div className='app-wrapper'>
          <HeaderContainer />
          <Navbar />
          <div className='app-wrapper-content'>
            <Routes>
              {/* <Route path='/profile' element={<Profile store={props.store} />} />
            <Route path='/dialogs' element={<DialogsContainer store={props.store}/>} /> */}
              <Route path='/profile/:userId?' element={<ProfileContainer />} />
              
              <Route path='/dialogs' element={<Suspense fallback={<Preloader />}><DialogsContainer /></Suspense>} />
              <Route path='/users' element={<UsersContainer />} />
              <Route path='/login' element={<LoginPage />} />
            </Routes>
          </div>
        </div>
      </HashRouter>
    );
  }
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

export default compose ( 
  withRouter,
  connect(mapStateToProps, { initializeApp }))(App); // App;
