import axios from "axios";

const instantAxios = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        "API-KEY": 'e7da44d3-f1a8-4627-ac34-2a9e04c39dcc'
    }
})

export const userAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instantAxios
            .get('users?page=' + currentPage + '&count=' + pageSize).then(response => {
                return response.data
            })
    },

    followUser(userId) {
        return instantAxios
            .post('follow/' + userId)
            .then(response => {
                return response.data
            })
    },

    unfollowUser(userId) {
        return instantAxios
            .delete('follow/' + userId)
            .then(response => {
                return response.data
            })
    },

    getProfile(userId) {
        return profileAPI.getProfile(userId);
        
        // instantAxios
        //     .get('profile/' + userId).then(response => {
        //         return response.data
        //     })
    }
}

export const authAPI = {
    me() {
        return instantAxios
            .get('auth/me')
            .then(response => {
                return response.data
            })
    },

    login(email, password, rememberMe = false) {
        return instantAxios
            .post('auth/login', { email, password, rememberMe })
            .then(response => {
                return response.data
            })
    },

    logout() {
        return instantAxios
            .delete('auth/login')
            .then(response => {
                return response.data
            })
    }
}

export const profileAPI = {

    getStatus(userId) {
        return instantAxios
            .get('profile/status/' + userId).then(response => {
                return response.data
            })
    },


    getProfile(userId) {
        return instantAxios
            .get('profile/' + userId).then(response => {
                return response.data
            })
    },

    updateStatus(status) {
        return instantAxios
            .put('profile/status', { status: status }).then(response => {
                return response.data
            })
    }
}