import React from 'react';
import { connect } from 'react-redux';
import Users from './Users';
import { follow,  unfollow, setCurrentPage, toggleIsFollowingProgress, requestUsers } from '../../redux/userReducer';
import Preloader from '../common/Preloader/Preloader';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { compose } from 'redux';
import { getUsers, getPageSize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress } from '../../redux/userSelectors';

class UsersContainer extends React.Component {
    componentDidMount() {  
        this.props.requestUsers(this.props.currentPage, this.props.pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.requestUsers(pageNumber, this.props.pageSize);
    }

    render() {

        return (
            <div>
                <div>
                    {this.props.isFetching ? <Preloader isFetching = {this.props.isFetching}/> : null}
                </div>
                
                <Users
                    totalUsersCount={this.props.totalUsersCount}
                    pageSize={this.props.pageSize}
                    currentPage={this.props.currentPage}
                    onPageChanged={this.onPageChanged.bind(this)}
                    users={this.props.users}
                    follow={this.props.follow}
                    unfollow={this.props.unfollow}
                    toggleIsFetching={this.props.toggleIsFetching}
                    followingInProgress={this.props.followingInProgress}
                />
            </div>
        )
    }
}

