const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    messageData: [
        { id: 1, message: 'Hi' },
        { id: 2, message: 'How are you' },
        { id: 3, message: 'Yo mayo 1' },
        { id: 4, message: 'Yo tvoyo 2' }
    ],
    dialogData: [
        { id: 1, name: 'Dialog 1' },
        { id: 2, name: 'Dialog 2' },
        { id: 3, name: 'Dialog 3' },
        { id: 4, name: 'Dialog 4' },
        { id: 5, name: 'Dialog 5' },
        { id: 6, name: 'Dialog 6' },
        { id: 7, name: 'Dialog 7' },
        { id: 8, name: 'Dialog 8' }
    ]
}

const dialogReducer = (state = initialState, action) => {
if (action.type === SEND_MESSAGE) {
        let body = action.newMessageBody;
        return {
            ...state, 
            messageData: [...state.messageData, {id: 6, message: body}] // if set befor, then set on zero pos
        }
    }
    return state
}

export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSAGE, newMessageBody: newMessageBody});


export default dialogReducer